@import '../../node_modules/swiper/css/swiper.css';

.nd-slider {
  $b: &;

  position: relative;
  display: block;
  width: 100%;

  &__container {
    position: relative;
    overflow: hidden;
    z-index: 1;

    @include screen('md', max, 2) {
      overflow: visible;
    }
  }

  &__buttons {
    position: absolute;
    top: 0;
    left: 0;
    display: flex;
    justify-content: space-between;
    align-items: center;
    box-sizing: border-box;
    width: 100%;
    height: 100%;
    pointer-events: none;
    z-index: 2;
  }

  &__button {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 150px;
    height: 100%;
    pointer-events: auto;
    cursor: pointer;
    fill: $nd-color-border-light;
    transition: fill $ease-main, opacity $ease-main;

    @include screen('xl', max, 2) {
      width: 60px;
    }

    @include screen('xlg', max, 2) {
      width: pxtovw(60, 1660);
    }

    @include screen('lg', max, 2) {
      width: pxtovw(50, 1366);
    }

    @include screen('md', max, 2) {
      width: pxtovw(50, 960);
    }

    @include screen('sm', max, 2) {
      width: pxtovw(20, 375);
    }

    &:hover {
      fill: $nd-color-basic;
    }

    &.swiper-button-disabled {
      opacity: 0;
      pointer-events: none;
    }
  }

  &__button-icon {
    position: relative;
    stroke: inherit;
    width: 15px;
    height: 40px;

    @include screen('xl', max, 2) {
      width: 12px;
      height: 30px;
    }

    @include screen('xlg', max, 2) {
      width: pxtovw(12, 1660);
      height: pxtovw(30, 1660);
    }

    @include screen('lg', max, 2) {
      width: pxtovw(8, 1366);
      height: pxtovw(21, 1366);
    }

    @include screen('md', max, 2) {
      width: pxtovw(8, 960);
      height: pxtovw(21, 960);
    }

    @include screen('sm', max, 2) {
      width: pxtovw(8, 375);
      height: pxtovw(21, 375);
    }

    #{$b}__button_next & {
      transform: rotate(180deg);
    }

    svg {
      position: relative;
      width: 100%;
      height: 100%;
      fill: inherit;
    }
  }

  &__slide {
    height: auto;

    &_card {
      @include screen('sm', max, 2) {
        width: pxtovw(250, 375);
        margin-right: 0;
        // margin-right: pxtovw(20, 375);
      }

      &:not(:first-child) {
        @include rule(margin-left, (sm: 20));
      }
    }

    &_space {
      @include screen('md', max, 2) {
        width: pxtovw(250, 960);
        margin-right: 0;
        // margin-right: pxtovw(20, 960);
      }

      @include screen('sm', max, 2) {
        width: pxtovw(250, 375);
        // margin-right: pxtovw(20, 375);
      }

      &:not(:first-child) {
        @include rule(margin-left, (md: 20, sm: 20));
      }
    }

    &_news {
      @include screen('md', max, 2) {
        width: pxtovw(354, 960);
        margin-right: 0;
        // margin-right: pxtovw(50, 960);
      }

      @include screen('sm', max, 2) {
        width: pxtovw(258, 375);
        // margin-right: pxtovw(37, 375);
      }

      &:not(:first-child) {
        @include rule(margin-left, (md: 50, sm: 37));
      }
    }

    &_map-card {
      @include screen('md', max, 2) {
        width: pxtovw(280, 960);
        margin-right: 0;
        // margin-right: pxtovw(20, 960);
      }

      @include screen('sm', max, 2) {
        width: pxtovw(280, 375);
        // margin-right: pxtovw(17, 375);
      }

      &:not(:first-child) {
        @include rule(margin-left, (md: 20, sm: 17));
      }
    }
  }
}
