/* ##########
Убираем outline
########## */

// * {
//   box-sizing: border-box;
//   border: 1px solid red;
// }

button::-moz-focus-inner {
  border: 0;
}

*:focus {
  outline: none;
}

/* ##########
Цвет выделения текста
########## */

::selection {
  background: $nd-color-main;
  color: white;
}

/* ##########
Стандартный цвет плейсхолдера
########## */

input, textarea {
  &::placeholder {
    color: $nd-color-placeholder;
  }
}

/* ##########
Настройки для автозаполнения Chrome
########## */

input:-webkit-autofill {
  -webkit-text-fill-color: $nd-color-basic !important; // Поменять цвет на аналогичный цвету input
}

input:-webkit-autofill, input:-webkit-autofill:hover, input:-webkit-autofill:focus, input:-webkit-autofill:active, textarea:-webkit-autofill, textarea:-webkit-autofill:hover, textarea:-webkit-autofill:focus, textarea:-webkit-autofill:active {
  animation: cursor .3s ease 1 !important;
  transition: background-color 50000000000s ease-in-out 0s !important; // Убираем желтую заливку для автозаполнения в Chrome
}

textarea:-internal-autofill-selected, input:-internal-autofill-selected {
  background-color: transparent !important;
}

/* ##########
Основные стили
########## */

html, body {
  margin: 0;
  padding: 0;
  // Убираем горизонтальный скролл
  width: 100%;
  min-height: 100%;
}

body {
  font-family: $font-main; // Задаем основной шрифт
  font-size: 18px;
  color: $nd-color-basic;
  background: $color-bg;
  overflow-x: hidden;

  &.nd {
    color: $nd-color-basic;
    letter-spacing: normal;

    @include screen('xlg', max, 2) {
      font-size: pxtovw(18, 1660);
    }

    @include screen('lg', max, 2) {
      font-size: pxtovw(17, 1366);
    }

    @include screen('md', max, 2) {
      font-size: pxtovw(17, 960);
    }

    @include screen('sm', max, 2) {
      font-size: pxtovw(17, 375);
    }
  }
}

img {
  box-sizing: content-box;
}

/* ##########
Сброс стандартных отступов для типографики, списков и таблиц
########## */

h1, h2, h3, h4, h5, h6, p, span, ul, ol, li, tr, td, th, table {
  margin: 0;
  padding: 0;
}

ul, ol {
  list-style: none;
}

span {
  font-style: inherit;
  font-family: inherit;
  font-weight: inherit;
}

a {
  text-decoration: none;
  color: currentColor;
  cursor: pointer;
}

/* ##########
Сброс стилей для активных элементов, добавляем стандарнтый transition
########## */

a, input, button, select, textarea {
  -webkit-tap-highlight-color: rgba(255, 255, 255, 0);
  transition: color $ease-main, border-color $ease-main;
}

label {
  -webkit-tap-highlight-color: rgba(255, 255, 255, 0);
}

button {
  cursor: pointer;
}

select, input, textarea, button {
  color: $nd-color-basic;
  margin: 0;
  padding: 0;
  border: 0;
  border-radius: 0; // Убираем авто-закругление углов на iOS
  background-color: transparent;
  font-family: $font-main;
  -webkit-appearance: none;
}

hr {
  display: block;
  margin: 0;
  padding: 0;
  border: none;
  width: 100%;
  height: 1px;
  -webkit-appearance: none;
  appearance: none;
  background-color: $color-border-light;
}
